<template>
  <div id="app">
    <div class="container">
      <div class="descr">
        <h2>Услуги:</h2>
        <ul>
          <li>Вызов врача на дом,</li>
          <li>Удаление зуба,</li>
          <li>Блефаропластика,</li>
          <li>Лечение кариеса,</li>
          <li>Коронки на зубы,</li>
          <li>Отбеливание zoom,</li>
          <li>ЭКО,</li>
          <li>Ринопластика,</li>
          <li>Удаление папиллом,</li>
          <li>Вызов детского врача / педиатра на дом</li>
        </ul>
      </div>

      <h3>Клиника Доктора Пеля на Васильевском</h3>
      <div class="select-filter" 
        :class="{'active': searchIsActive}">

        <transition-group class="selected-items" tag="div" name="list">
          <span 
            v-for="(item, index) in selectedServices"
            :key="item">{{ item }} <i @click="removeService(index)"></i></span>
        </transition-group>

        <input type="text" 
          ref="searchInput"
          id="select-filter-text-input"
          placeholder="Добавить услугу"
          v-model.trim="searchService"
          @focus="searchIsActive = true"
          @blur="searchIsActive = false"
          @input="emulateRequest">
        <ul>
          <li 
            v-for="(item, index) in filtredServices" 
            :key="index"
            @click="addService(item)">{{ item }}</li>
            <li v-if="searchService.length >= 3 && filtredServices.length == 0">Совпадений не найдено</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchIsActive: false,
      searchService: '',
      allServices: [
        'Вызов врача на дом',
        'Удаление зуба',
        'Блефаропластика',
        'Лечение кариеса',
        'Коронки на зубы',
        'Отбеливание zoom',
        'ЭКО',
        'Ринопластика',
        'Удаление папиллом',
        'Вызов детского врача / педиатра на дом'
      ],
      selectedServices: [],
    }
  },

  computed: {   
    filtredServices() {
      if(this.searchService.length >= 3) {
        return this.allServices.filter(service => {
          return (service.toLowerCase().indexOf(this.searchService.toLowerCase()) !== -1 && this.selectedServices.indexOf(service) == -1);
        });
      }
    },
  },

  methods: {
    addService(item) {
      this.selectedServices.push(item);
      this.searchService = '';
      this.$refs.searchInput.focus();
    },
    removeService(index) {
      this.selectedServices.splice(index, 1);
    },
    emulateRequest() {
      if(this.searchService.length == 3) {
        this.$refs.searchInput.classList.add('searching');
        setTimeout(() => {
          this.$refs.searchInput.classList.remove('searching');
        }, 2000);
      }
    }
  }
};
</script>

